*{
  box-sizing: border-box;
}
.margin-r-20{
  margin-right: 20px;
}
.border-top{
  border-top: 1px solid #ddd;
}
.text-right{
  text-align: right;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.buttons {
  padding: 10px 20px;
  background-color: #4e8be3;
  color: #fff;
  border: 0;
  border-radius: 2px;
  font-size: 1em;
  cursor: pointer; }
  .buttons:hover, .buttons:focus {
    background-color: #203c63; }

.campaign-title {
  padding: 15px; }

.campaign {
  padding: 20px 30px;
  border-bottom: 1px solid #ddd;
  color: #999; }
  .campaign > div {
    display: inline-block;
    vertical-align: middle; }
  .campaign.active {
    color: #222; }
    .campaign.active i + span {
      display: block; }
    .campaign.active .campaign__no span {
      background-color: #4e8be3;
      color: #f7fafe; }
    .campaign.active .campaign__pause {
      color: #e7c700; }
    .campaign.active .campaign__comment,
    .campaign.active .campaign__rename {
      color: #4e8be3; }
    .campaign.active .campaign__delete {
      color: #c6181c; }
  .campaign i + span {
    display: none; }
  .campaign__no, .campaign__pause, .campaign__comment, .campaign__rename, .campaign__delete {
    width: 10%;
    text-align: center; }
  .campaign__title {
    width: 50%; }
  .campaign__no span {
    background-color: #f7fafe;
    color: #4e8be3;
    border-radius: 100%;
    padding: 10px 15px;
    text-align: center; }
  .campaign__title p {
    margin: 0;
    font-size: 12px; }
  .campaign__title h3 {
    margin-bottom: 5px;
    margin-top: 0; }


.history {
  background-color: #f7fafe;
  padding: 15px; }
  .history__title {
    color: #666;
    margin-bottom: 15px;
    font-size: 18px; }
    .history__title i {
      margin-right: 15px; }
  .history__name {
    font-weight: bold;
    margin-bottom: 50px;
    margin-left: 30px; }
  .history__list {
    margin-left: 30px; }
    .history__list > div {
      margin-bottom: 30px; }

.campaign-modal__overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #000;
  opacity: 0.8; }

.campaign-modal__body {
  position: relative;
  z-index: 1;
  background-color: #fff;
  padding: 30px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border-radius: 3px;
  width: 40%;
  margin: 0 auto; }

.campaign-modal form > div {
  margin-bottom: 20px; }

.campaign-modal form label {
  width: 30%;
  display: inline-block; }

.campaign-modal form input {
  padding: 10px 15px;
  border-radius: 3px;
  border: 1px solid #ddd;
  outline: none;
  width: 70%; }

.app {
  margin: 0;
  padding: 0;
  font-size: 1em; }
  .app__header {
    background-color: #f7fafe;
    padding: 15px; }
  .app__campaigns {
    width: 70%;
    display: inline-block;
    border-right: 1px solid #ddd; }
  .app__history {
    width: 30%;
    display: inline-block;
    vertical-align: top; }

